import React, { useEffect, useState } from 'react';
import { Button, Input } from 'reactstrap';

export const Food = ({ item, editable, handleProtOnChange }) => {
  const [_item, setItem] = useState(item);
  const [cals, setCals] = useState();
  const [isEditable, setIsEditable] = useState(editable);
  const [isDone, setIsDone] = useState(false);

  useEffect(() => {
    setIsEditable(editable);
    calulateCalories();
  }, [editable]);

  const calulateCalories = () => {
    setCals(Math.round((_carb + _prot) * 4 + _fat * 9));
  };

  const handleOnclickEdit = e => {
    setIsEditable(true);
  };

  const handleOnclickDone = e => {
    setIsEditable(false);
  };

  const _handleProtOnChange = value => {
    setProt(Number(value));
    calulateCalories();
  };

  return (
    <tr>
      <th scope="row">{_id}</th>
      <td>{isEditable ? <Input type="text" value={_name} /> : _name}</td>
      <td>{isEditable ? <Input type="text" value={_group} /> : _group}</td>
      <td>{isEditable ? <Input type="number" value={_portion} /> : _portion + ' ' + _units}</td>
      <td>{cals}</td>
      <td>
        {isEditable ? (
          <Input
            type="number"
            value={_prot}
            onChange={event => handleProtOnChange({ ..._, prot: event.target.value })}
          />
        ) : (
          _prot + ' g'
        )}
      </td>
      <td>
        {isEditable ? (
          <Input
            type="number"
            value={_carb}
            onChange={event => handleCarbOnChange(event.target.value)}
          />
        ) : (
          _carb + ' g'
        )}
      </td>
      <td>
        {isEditable ? (
          <Input
            type="number"
            value={_fat}
            onChange={event => handleFatOnChange(event.target.value)}
          />
        ) : (
          _fat + ' g'
        )}
      </td>
      <td>
        <Button
          color={!isEditable ? 'primary' : 'danger'}
          onClick={!isEditable ? handleOnclickEdit : handleOnclickDone}
        >
          {!isEditable ? 'edit' : 'done'}
        </Button>
      </td>
    </tr>
  );
};
